.message {
  box-sizing: border-box;
  max-width: 400px;
  margin-bottom: 10px;
  list-style: none;
  overflow: hidden;
}

.message:last-of-type {
  margin-bottom: 30px;
}

.message_is-not-owner {
  align-self: flex-end;
}

.message_is-img {
  border: 1px solid #eaeaea;
  border-radius: 10px;
  padding: 11px;
}

.message__text {
  color: #1e1e1e;
  border-radius: 10px;
  margin: 0;
  padding: 11px;
  font-size: 10px;
  font-weight: 500;
  line-height: 15px;
  display: inline-block;
}

.message__text_is-me {
  background-color: #f8f8f8;
}

.message__text_is-friend {
  background-color: #e0e6ef;
}

.message__img {
  max-width: 402px;
  width: 100%;
  object-fit: contain;
}

.message__time {
  color: #999;
  text-align: right;
  float: right;
  margin: 6px 0 0 30px;
  font-size: 9px;
  font-weight: 500;
  line-height: 9px;
  position: relative;
}

.message__time_is-img {
  color: #fff;
  background-color: #a8a8a8;
  border-radius: 30px;
  padding: 2px 7px;
}

